* {
  background-color: black;
}

.main {
  display: flex;
  flex-direction: column;
}

body {
padding-left: 40px;
padding-right: 40px;
}

h1 {
  color: white;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 10px;
}

h3 {
  color: white;
  font-weight: 300;
  margin-left: 100px;
  margin-right: 100px;

}

h3, #cross {
  width: 900px;
  padding: 10px;
  padding-left: 30px;
  padding-right: 30px;
  font-family: sans-serif;
  font-size: 25px;
  text-decoration: none;
  background-color: #444040;
  color: white;
  padding-bottom: 15px;
}

.netflixbtn-position {
  position: absolute; 
  top: 75%;
  left: 48%;
  transform: translate(-50%, -50%); 
}

.netflixbtn-style {
  text-transform: uppercase;
  width: 300px;
  padding: 10px;
  font-family: sans-serif;
  font-size: 25px;
  text-align: center;
  text-decoration: none;
  background-color: #b31c1c;
  color: white;
  padding-bottom: 15px;
  font-weight: lighter;
}

.indicator {
  font-size: xx-large;
  background-color: #b31c1c;
  padding-left: 20px;
}

#cross {
  font-size: 30px;
  justify-content: space-between;
}